import { z } from "zod"
import { bigdataApiProcedure } from "../bigdata-api-procedure"

const res = () => ({
  slackUserId: "",
  workspaceId: "",
  workspaceName: "",
  access: false,
})

export const slackProcedures = {
  validateAccess: bigdataApiProcedure
    .meta({ openapi: { method: "POST", path: "/bot/slack/validate-access" } })
    .input(z.object({}))
    .output(z.custom<ReturnType<typeof res>>())
    .query(async () => res()),
}
